import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { VhAlgorithm, VhQueryCafe, VhAuth } from 'ionic-vhframeworks';
import { VhType } from 'src/app/cafe/interface/vh-type';

@Component({
  selector: 'app-search-service',
  templateUrl: './search-service.component.html',
  styleUrls: ['./search-service.component.scss'],
})
export class SearchServiceComponent implements OnInit {
  // chng trnh khuyn mi ang c p dng
  @Input('promotions') promotions: any[] = [];
  @Input('showDrawerServiceList') showDrawerServiceList: boolean = true;
  // tr v product c chn v  x l cho bill-detail1
  @Output('closeSearchServiceList') closeSearchServiceList: EventEmitter<any> = new EventEmitter();
  /**Danh sch dch v */
  public serviceList: any[] = this.vhQueryCafe
    .getlocalDetailProducts(VhType.SERVICE_SERVICETIME)
    .filter((item) => !item.delete_hidden)
    .map((e) => {
      return { ...e, ...e.units[0] };
    });
  /**Dch v c chn */
  choiceService: any = {};
  /** Danh sch dch v  c tm kim */
  public searchList: any[] = [];
  /** Danh mc dch v */
  list_menu: any = [];
  listTabs: Array<any> = [{ _id: 'all', name: 'All', type: 1 }];
  indexTab: number = 0;
  @Input() default_price_type: number;

  public VhType = VhType;

  constructor(
    public vhAlgorithm: VhAlgorithm,
    private vhQueryCafe: VhQueryCafe,
    public vhAuth : VhAuth
  ) {}
  ngOnInit(): void {
    this.list_menu = this.vhQueryCafe.getlocalCategorys(VhType.SERVICE);
    this.list_menu.forEach((item) => this.listTabs.push(item));
    this.searchList = [...this.serviceList];
    this.searchList = this.vhAlgorithm.sortVietnamesebyASC(
      this.searchList,
      'name'
    );
  }
  ngAfterViewInit() {
    document.getElementById('inputSearchService').focus();
  }

  myTrackByFunction(element: any, index: any) {
    return element._id ? element._id : index;
  }

  goBack() {
    this.closeSearchServiceList.emit(false);
  }
  tempVal: string = '';
  /**
   * Tm kim dch v
   * @param value gi tr tm kim
   */
  searchService(value: string) {
    let tempVal: string = this.vhAlgorithm.changeAlias(value.toLowerCase());

    if (value.length)
      this.searchList = this.vhAlgorithm.searchList(tempVal, this.serviceList, [
        'name',
      ]);
    else this.searchList = this.serviceList;
    this.searchList = this.vhAlgorithm.sortVietnamesebyASC(
      this.searchList,
      'name'
    );
  }
  /**
   * Chn dch v  x l
   * @param value d liu dch v c chn
   */
  selectService(value) {
    let dispatcher_done = !this.vhQueryCafe.getLocalAppSettingNameBranch('dispatchers_enable').dispatchers_enable;
    this.choiceService = {
      dispatcher_done: dispatcher_done,
      ...value,
      quantity: 1,
      note: '',
      price_origin:
        this.default_price_type == 1
          ? value.price
          : value.price2
          ? value.price2
          : value.price,
      price:
        this.default_price_type == 1
          ? value.price
          : value.price2
          ? value.price2
          : value.price,
      ptype: value.type,
    };

    if (this.choiceService) {
      if (value.type !== VhType.SERVICETIME) {
        this.showOptions = true;
        setTimeout(() => {
          this.editPrice['price-' + value._id] = this.vhAlgorithm.vhnumeral('.price-' + value._id);
        }, 200);
      }
      else {
        this.closeSearchServiceList.emit(this.choiceService);
      }
    }
  }

  editPrice: any = {};
  /**Thm cleavejs chnh sa gi mn n */
  updatePrice(item) {
    let price = this.editPrice['price-' + item._id].getRawValue();
    this.choiceService.price = price ? parseFloat(price) : 0;
    this.choiceService.quantity =
      this.choiceService.quantity > 0 ? this.choiceService.quantity : 1;
  }
  chooseService() {
    this.showOptions = false;
    if (this.choiceService['quantity']) {
      this.closeSearchServiceList.emit(this.choiceService);
    }
  }

  changeTypeGood(event) {
    this.indexTab = event;
    let products = this.vhQueryCafe
      .getlocalDetailProducts(VhType.SERVICE_SERVICETIME)
      .filter((item) => !item.delete_hidden)
      .map((e) => {
        return { ...e, ...e.units[0] };
      });
    if (event == 0) {
      this.serviceList = products;
    } else {
      this.serviceList = products.filter(
        (item) => item.id_category == this.listTabs[event]._id
      );
    }
    this.searchService(this.tempVal);
  }
  showOptions: boolean = false;
  selectFoodsCancel() {
    this.showOptions = false;
  }

  /** Tng s lng sn phm */
  increaseQuantity(item) {
    if (item.quantity != '') item.quantity = parseFloat(item.quantity);
    item.quantity = item.quantity + 1;
  }
  /** Gim s lng sn phm */
  reduceQuantity(item) {
    if (item.quantity != '') item.quantity = parseFloat(item.quantity);
    item.quantity = item.quantity - 1;
  }
}
