import { Bill1Module } from './bill1/bill1.module';

import { Customer1Module } from './customer1/customer1.module';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';
import { RouterModule, Routes } from '@angular/router';
import { Cart1Component } from './cart1.component';
import { TranslateModule } from '@ngx-translate/core';
import { BillDetail1Module } from '../cart1/bill-detail1/bill-detail1.module';
import { InfoBill1Module } from '../cart1/info-bill1/info-bill1.module';
import { PaymentButton1Module } from '../cart1/payment-button1/payment-button1.module';
import { CanLeavePage } from 'src/app/cafe/desktop/canLeavePage';

const routes: Routes = [
  {
    path: "",
    component: Cart1Component,
    canDeactivate: [CanLeavePage]
  }
]

@NgModule({
  declarations: [Cart1Component],
  imports: [
    CommonModule,
    FormsModule,
    NgZorroAntModule,
    TranslateModule,
    RouterModule.forChild(routes),
    BillDetail1Module,
    Customer1Module,
    InfoBill1Module,
    Bill1Module,
    PaymentButton1Module
  ]
})
export class Cart1Module { }
