import { VhAuth, VhQueryCafe } from 'ionic-vhframeworks';
import { Component, EventEmitter, HostListener, Input, OnInit, Output } from '@angular/core';
import { VhOrderInvoices } from 'src/app/cafe/interface/vh-order-invoice';
import { ProductService } from 'src/app/cafe/services/product.service';

@Component({
  selector: 'app-payment-button1',
  templateUrl: './payment-button1.component.html',
  styleUrls: ['./payment-button1.component.scss']
})
export class PaymentButton1Component {
  @HostListener('window:keydown', ['$event'])
  keyEvent(event: KeyboardEvent) {
    if (["F1", "F2", "Delete"].includes(event.key)) {
      switch (event.key) {
        case "F1": document.getElementById("order-and-print").click(); break;
        case "F2": document.getElementById("order").click(); break;
        case "Delete": document.getElementById("delete-invoice").click(); break;
      }
      event.preventDefault()
      event.stopPropagation()
    }
  }
  // i tng ha n
  @Input() invoices: VhOrderInvoices;
  // biu hin trang thi - 1/ ang cp nht ha n - 2/ ang xa ha n
  @Input() statusInvoice: any = {};
  @Input() stamp: any; // thng tin tem in truyn t component card
  // tin hnh thanh ton 
  @Output() payment: EventEmitter<"print" | "notPrint"> = new EventEmitter();
  // tin hnh xa ha n
  @Output() deleteInvoice: EventEmitter<boolean> = new EventEmitter();
  @Output() testPrint: EventEmitter<boolean> = new EventEmitter();
  @Output() printAllLabel: EventEmitter<boolean> = new EventEmitter();
  @Output() printKitchenAll: EventEmitter<boolean> = new EventEmitter();

  checkPrinter: boolean = false;
  checkPrinterKitchen: boolean = false;
  /** thuc tnh my in */
  printer: any;
  colSpan = '12';
  openBillStatus: boolean = false;



  constructor(
    private vhQueryCafe: VhQueryCafe,
    public vhAuth: VhAuth,
    private productService: ProductService,
  ) { }

  ngOnChanges(): void {
    // kim tra my in
    let printer = this.vhQueryCafe.getLocalPrintPage('page_desktop_sales')
    this.printer = printer ? printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)] : null
    if (this.printer) {
      this.checkPrinter = this.printer.enable
    }
    let printer_kitchen = this.vhQueryCafe.getLocalPrintPage('page_desktop_kitchen')
    this.checkPrinterKitchen =  printer_kitchen ? printer_kitchen.print_sizes[printer_kitchen.print_sizes.findIndex(e => e._id == printer_kitchen.print_size_default)].enable : null
    if (this.checkPrinterKitchen && this.stamp) this.colSpan = '24'
    if (!this.checkPrinterKitchen && !this.stamp) this.colSpan = '24';
    if ((!this.checkPrinterKitchen && this.stamp) || (this.checkPrinterKitchen && !this.stamp)) this.colSpan = '12' 
  }

  saveInvoice(print: "print" | "notPrint" = "notPrint") {
    this.payment.emit(print)
  }

  delete() {
    this.deleteInvoice.emit(true)
  }

  handlePrintAllLabel() {
    this.printAllLabel.emit(true)
    setTimeout(() => {
      this.printAllLabel.emit(false)
    }, 1000);
  }

  handleCancel() {
    this.openBillStatus = false
  }



  /**
   * Tnh li bill detail cho Dch v thi gian v setSubTotal() cho invoices
   */
  updateServiceTime() {
    if(!this.productService.isContainServiceTime(this.invoices)) return;
    this.productService.updateServiceTimePriceInvoiceDetail(this.invoices.getInvoiceDetail(), this.invoices.getPriceType());
    this.invoices.setSubTotal();
  }

}
