  destroyBillDetails_withPromotions() {
    this.invoices.setBillHavePromotion(false);
    return new Promise((resolve) => {
      let bill_details = this.vhQueryCafe
        .destroyBillDetails_withPromotions(this.invoices.getInvoiceDetail())
        .map((item) => {
          return { ...item, _id: item._id ? item._id : true };
        });
      console.log(bill_details);

      this.invoices = new VhOrderInvoices(
        this.invoices.getCreateUpdateInvoice(5),
        bill_details,
        this.vhQueryCafe,
        this.plaform
      );
      this.invoices.setSubTotal();
      this.product_gift_type7 = [];
      this.bill_details_pmtype_7 = [];
      resolve(true);
    });
  }
  isGotPromotion = false;
  showProductGift = false;
  product_gift_type7: any = [];
  showProductGiftType7 = false;
  gotten_gift_type7 = false;
  bill_details_pmtype_7: any = [];
  handleAddGiftType7() {
    this.showProductGiftType7 = false;
    if (!this.gotten_gift_type7)

      for (let i in this.product_gift_type7) {
        if (this.product_gift_type7[i].products_gift.filter(e => e.choose).length)
          this.invoices.addBill_detail_promotion(this.product_gift_type7[i]);
      }
    this.gotten_gift_type7 = true;
    console.log(this.invoices.getInvoiceDetail());

  }
  /**
   * hm ly qu
   */
  getPromotions() {
    this.gotten_gift_type7 = false;
    this.vhComponent.showLoading('').then(() => {
      /** Cha cc bill_detail c ptype = 6 */
      let bill_details_type_6;
      /** Cha cc bill_detail c ptype != 6 */
      let bill_details_others;
      // Nu c Dch v thi gian th tch bill_detail c ptype = 6 ra  x l ring, sau khi get xong th thm v li
      if(this.productService.isContainServiceTime(this.invoices)) {
        bill_details_type_6 = this.invoices.getInvoiceDetail().filter((item) => item.ptype === VhType.SERVICETIME);
        bill_details_others = this.invoices.getInvoiceDetail().filter((item) => item.ptype !== VhType.SERVICETIME);
        this.invoices = new VhOrderInvoices(
          this.invoices.getCreateUpdateInvoice(5),
          bill_details_others,
          this.vhQueryCafe,
        );
      }
      this.vhQueryCafe
        .changeBillDetails_withPromotions(
          this.invoices.getInvoiceDetail(),
          this.invoices.getDate()
        )
        .then(
          (bill_details_display: any) => {
            // Nu c Dch v thi gian th thm bill_detail c ptype = 6 vo li
            if(this.productService.isContainServiceTime(this.invoices)) {
              bill_details_display.bill_details_pmtype_0to6 = [
                ...bill_details_type_6,
                ...bill_details_display.bill_details_pmtype_0to6,
              ];
            }

            this.handleChooseLotForPromotion(
              bill_details_display.bill_details_pmtype_0to6
            );
            this.handleChooseLotForPromotion(
              bill_details_display.bill_details_pmtype_7
            );
            this.bill_details_pmtype_7 = this.vhAlgorithm.sortNumberbyASC(
              [...bill_details_display.bill_details_pmtype_7],
              'total_bill_value'
            );
            this.isGotPromotion = true;
            this.vhComponent.hideLoading(0);
            this.invoices = new VhOrderInvoices(
              this.invoices.getCreateUpdateInvoice(5),
              [...bill_details_display.bill_details_pmtype_0to6],
              this.vhQueryCafe,
              this.plaform
            );
            this.handlePromotionType7(this.bill_details_pmtype_7);
            this.getEarningpointPromotion().then(() => {
              this.vhQueryCafe.updateBill_Billdetail(
                this.invoices.getID(),
                this.invoices.getCreateUpdateInvoice(5),
                this.invoices.getCreateUpdateInvoiceDetail()
              ).then(() => {
                console.log("success");
              })
            })
          },
          (error) => {
            this.vhComponent.hideLoading(0).then(() => {
              this.vhComponent.showToast(5000, error, 'alert-toast');
            });
          }
        );
    });
  }

  handlePromotionType7(bill_details_pmtype_7) {
    if (bill_details_pmtype_7.length) {
      if (
        bill_details_pmtype_7.findLastIndex(
          (e) => e.total_bill_value <= this.invoices.getTotalATax()
        ) != -1
      )
        this.product_gift_type7 = [
          bill_details_pmtype_7[
          bill_details_pmtype_7.findLastIndex(
            (e) => e.total_bill_value <= this.invoices.getTotalATax()
          )
          ],
        ];
      else this.product_gift_type7 = [];
    } else this.product_gift_type7 = [];
  }

  handleChooseLotForPromotion(promotion) {
    for (let i of promotion) {
      if (i.products_gift) {
        for (let product of i.products_gift) {
          if (product.lots && product.lots.length) {
            let min_date_exp = this.vhAlgorithm.sortDatebyASC(
              product.lots,
              'date_exp'
            );
            product['id_lotproduct'] = min_date_exp[0]._id;
            product['lot_number'] = min_date_exp[0].lot_number;
          }
          if (product.combos) {
            product.combos.forEach((element) => {
              if (element.lots && element.lots.length) {
                let min_date_exp = this.vhAlgorithm.sortDatebyASC(
                  element.lots,
                  'date_exp'
                );
                element['id_lotproduct'] = min_date_exp[0]._id;
                element['lot_number'] = min_date_exp[0].lot_number;
              }
            });
          }
        }
      }
    }
  }

  /**
  * hm ny  get SubTotal mi ca mng  ly qu
  * ng thi gn im tch ly sn phm mi ca cc sn phm trong khuyn mi nu c
  */
  getEarningpointPromotion() {
    return new Promise((resolve) => {
      for (let i of this.invoices.getInvoiceDetail()) {
        if (i.pmtype) this.invoices.setBillHavePromotion(true);
        if (i.products_gift) {
          for (let product of i.products_gift) {
            product.earning_points_product = this.getTotalEarningPointProduct(i, true);
          }
        }
        if (i.products) {
          for (let product of i.products) {
            product.earning_points_product = this.getTotalEarningPointProduct(i, true)
          }
        }
        if (!i.id_promotion) i.earning_points_product = this.getTotalEarningPointProduct(i, null)
      }
      this.invoices.setSubTotal();
      resolve(true);
    });
  }

  /**
   * hm ny  kim tra disable sn phm tng hay ko
   * @param data phn t trong mng  ly qu
   * @returns
   */
  checkDisableChooseProductGift(data) {
    if (
      data.products_gift.filter((item) => item.choose).length ==
      data.promotions_maximum
    )
      return true;
    else return false;
  }
