import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { VhAuth } from 'ionic-vhframeworks';
import { VhOrderInvoices } from 'src/app/cafe/interface/vh-order-invoice';

@Component({
  selector: 'app-info-bill1',
  templateUrl: './info-bill1.component.html',
  styleUrls: ['./info-bill1.component.scss']
})
export class InfoBill1Component implements OnInit {
  // i tng ha n
  @Input() invoices: VhOrderInvoices;
  // ID nhn vin ang order
  @Input() employeeID: string;
  // thng tin bn ang order
  @Input() tableInfo: any = {};
  // cp nht ha n khi c s thay i
  @Output() updateInvoice: EventEmitter<any> = new EventEmitter()

  employeeList: any[] = []
  constructor(
    public vhAuth: VhAuth
  ) { }

  ngOnInit(): void {
    this.initData()
  }

  myTrackByFunction(element: any, index: any) {
    return element._id ? element._id : index
  }

  initData() {
    // nhn vin
    this.employeeList = this.vhAuth.getlocalEmployees();
  }

  changeEmployee(ev) {
    this.invoices.setIdEmployee(ev)
    this.updateInvoice.emit({ id_employee: ev })
  }
}
