import { Component, EventEmitter, OnInit, Input, Output } from '@angular/core';
import { VhAlgorithm, VhAuth, VhQueryCafe } from 'ionic-vhframeworks';

@Component({
  selector: 'app-search-combo',
  templateUrl: './search-combo.component.html',
  styleUrls: ['./search-combo.component.scss'],
})
export class SearchComboComponent implements OnInit {
  @Input('showDrawerCombosList') showDrawerCombosList: boolean = true;
  // tr v product c chn v  x l cho bill-detail1
  @Output('closeSearchCombosList') closeSearchCombosList: EventEmitter<any> =
    new EventEmitter();
  public combosList: any[] = [];
  choiceCombos: any = {};
  public searchList: any[] = [];
  id_branch = this.vhQueryCafe.getDefaultBranch()._id;

  auto_hidden_lot: boolean =
    this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch')
      .auto_hidden_lot;
  auto_select_lot: boolean =
    this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch')
      .auto_select_lot;
  constructor(
    public vhAlgorithm: VhAlgorithm,
    private vhQueryCafe: VhQueryCafe,
    public vhAuth : VhAuth
  ) {}
  ngOnInit(): void {
    let dispatcher_done =
      !this.vhQueryCafe.getLocalAppSettingNameBranch('dispatchers_enable')
        .dispatchers_enable;
    this.combosList = this.vhQueryCafe
      .getlocalDetailProducts(5)
      .filter((item) => !item.delete_hidden)
      .map((e) => {
        this.handleLotsOfCombo(e.combos.filter((lot) => lot.lots));
        return {
          ...e,
          dispatcher_done: dispatcher_done,
          ...e.units[0],
          combos: e.combos.map((pro) => {
            return {
              ...pro,
              unit: this.getUnit(pro.units, pro.ratio),
              ptype: pro.type,
            };
          }),
        };
      });
    this.searchList = [...this.combosList];
    this.searchList = this.vhAlgorithm.sortVietnamesebyASC(
      this.searchList,
      'name'
    );
  }
  ngAfterViewInit() {
    document.getElementById('inputSearchCombos').focus();
  }

  handleLotsOfCombo(combos) {
    combos = this.vhAlgorithm.sortDatebyASC(combos, 'date_exp');
    for (let i of combos) {
      if (i.lots.length) {
        if (this.auto_hidden_lot)
          i.lots = i.lots.filter(
            (e) => new Date(e.date_exp).getTime() >= new Date().getTime()
          );
        let min_date_exp = this.vhAlgorithm.sortDatebyASC(i.lots, 'date_exp');
        // if (this.auto_select_lot)
        i['id_lotproduct'] = min_date_exp[0]._id;
      }
    }
  }

  myTrackByFunction(element: any, index: any) {
    return element._id ? element._id : index;
  }

  goBack() {
    this.closeSearchCombosList.emit(false);
  }

  tempVal: string = '';
  /** Tm kim combo
   * @param value gi tr tm kim
   */
  searchCombos(value: string) {
    let tempVal: string = this.vhAlgorithm.changeAlias(value.toLowerCase());

    if (value.length)
      this.searchList = this.vhAlgorithm.searchList(tempVal, this.combosList, [
        'name',
      ]);
    else this.searchList = this.combosList;
    this.searchList = this.vhAlgorithm.sortVietnamesebyASC(
      this.searchList,
      'name'
    );
  }
  /**
   * Chn combo  x l
   * @param value d liu combo c chn
   */
  selectCombos(value) {
    this.choiceCombos = {
      ...value,
      quantity: 1,
      note: '',
      price_origin: value.price,
      price: value.price,
      ptype: 5,
    };

    if (this.choiceCombos) {
      this.showOptions = true;
      setTimeout(() => {
        this.editPrice['price-' + value._id] = this.vhAlgorithm.vhnumeral(
          '.price-' + value._id
        );
      }, 200);
    }
  }

  editPrice: any = {};
  /**Thm cleavejs chnh sa gi mn n */
  updatePrice(item) {
    let price = this.editPrice['price-' + item._id].getRawValue();
    this.choiceCombos.price = price ? parseFloat(price) : 0;
    this.choiceCombos.quantity =
      this.choiceCombos.quantity > 0 ? this.choiceCombos.quantity : 1;
  }
  chooseCombos() {
    this.showOptions = false;
    if (this.choiceCombos['quantity']) {
      this.choiceCombos['combos'] = this.choiceCombos['combos'].map((item) => {
        return {
          ...item,
          id_category: this.vhQueryCafe.getlocalProduct(item.id_product)
            .id_category,
        };
      });

      this.closeSearchCombosList.emit(this.choiceCombos);
    }
  }
  showOptions: boolean = false;
  selectFoodsCancel() {
    this.showOptions = false;
  }

  changeLot(e, item) {
    item.lot_number = item.lots.find((find) => find._id == e).lot_number;
  }

  getUnit(units, ratio) {
    return this.vhQueryCafe.getUnit_byRatio(units, ratio).unit;
  }

  /** Tng s lng sn phm */
  increaseQuantity(item) {
    if (item.quantity != '') item.quantity = parseFloat(item.quantity);
    item.quantity = item.quantity + 1;
  }
  /** Gim s lng sn phm */
  reduceQuantity(item) {
    if (item.quantity != '') item.quantity = parseFloat(item.quantity);
    item.quantity = item.quantity - 1;
  }
}
